header {
	margin-top: 4.5rem;
	margin-bottom: 4.8rem;
}

h1 {
	font-family: $ffHeading;
	font-size: 2.4rem;
	text-align: center;
	color: $accent;

	@include respond-to(medium) {
		font-size: 3rem;
	}

	@include respond-to(big) {
		font-size: 4rem;
	}
}

.header__span {
	display: block;
}
